.menubar-wrap {
  height: 100%;
}
.menubar-wrap .menubar-box {
  height: 100%;
  width: 50px;
  background: #626262;
  position: relative;
}
.menubar-wrap .menubar-box .menubar-log {
  height: 38px;
  line-height: 38px;
  border-bottom: 1px #E5E5E5 solid;
  font-size: 12px;
  text-align: center;
  color: #E5E5E5;
}
.menubar-wrap .menubar-box .menubar-log img {
  margin: 15px auto;
  height: 8px;
  text-align: center;
}
.menubar-wrap .menubar-box .menubar-content ul li {
  height: 50px;
  width: 50px;
}
.menubar-wrap .menubar-box .menubar-content ul li img {
  height: 30px;
  width: 30px;
  margin: 10px;
}
.menubar-wrap .menubar-box .menubar-content ul li.active {
  background: #37332F;
}
.menubar-wrap .language-toggle {
  position: absolute;
  bottom: 30px;
  text-align: center;
  width: 100%;
}
.menubar-wrap .language-toggle img {
  cursor: pointer;
  height: 20px;
  width: 20px;
}
.menubar-wrap .language-toggle .language-info {
  font-family: NotoSansHans-Regular;
  font-size: 12px;
  color: #FFFFFF;
  text-align: center;
}



