.download-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
}
.download-wrapper .download-content {
  position: absolute;
  top: 50%;
  left: 50%;
  background: #37332F;
  -webkit-box-shadow: 0 2px 5px 0 rgba(34, 34, 34, 0.5);
          box-shadow: 0 2px 5px 0 rgba(34, 34, 34, 0.5);
  width: 600px;
  text-align: center;
  color: #FFFFFF;
  border-radius: 5px;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
     transform: translate(-50%, -50%);
}
.download-wrapper .download-content .download-close {
  float: right;
  margin-top: 20px;
  margin-right: 20px;
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.download-wrapper .download-content .download-close img {
  width: 100%;
}
.children-box .download-text {
  margin-top: 60px;
  margin-bottom: 15px;
  font-size: 14px;
  color: #FFFFFF;
  text-align: center;
}
.children-box .download-href {
  margin-bottom: 40px;
}
.children-box .download-href a {
  font-size: 14px;
  color: #1A83FF;
  text-align: center;
  text-decoration: underline;
}
.children-box .download-btn-wrapper {
  margin-bottom: 50px;
}



