.layout-container {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}
.layout-container .sidebar {
  width: 310px;
  height: 100%;
  overflow-y: inherit;
  background: #37332F;
}
.layout-container .sidebar .header {
  width: 310px;
  padding: 0 15px;
  /* margin-top: 10px; */
  height: 38px;
  line-height: 38px;
  font-size: 14px;
  color: #FFFFFF;
  border-bottom: #FFFFFF 1px solid;
  position: relative;
}
.layout-container .sidebar .header .network {
  /* float: right; */
  /* border-radius: 10px; */
  border-radius: 10px;
  width: 90px;
  height: 20px;
  text-align: center;
  line-height: 20px;
  position: absolute;
  top: 9px;
  right: 15px;
  font-size: 12px;
}
.layout-container .sidebar .header .network.neo3 {
  background: #43C22F;
}
.layout-container .sidebar .header .network.main {
  background: #1267CB;
}
.layout-container .sidebar .header .network.test {
  background: #757575;
}
.layout-container .sidebar .header a img {
  height: 16px;
  width: 16px;
  margin: 10px;
}
.layout-container .sidebar .sidebar-body {
  height: 95%;
  overflow: auto;
}
.layout-container .code-container {
  background: #161616;
  height: 100%;
  -webkit-flex: auto;
      -ms-flex: auto;
          flex: auto;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
}
.layout-container .code-container .code-header {
  height: 38px;
  line-height: 38px;
  font-size: 14px;
  color: #FFFFFF;
  border-bottom: #FFFFFF 1px solid;
}
.layout-container .code-container .code-main {
  -webkit-flex: auto;
      -ms-flex: auto;
          flex: auto;
}
.layout-container .black-wrap {
  position: fixed;
  top: 58px;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 1000;
}



