.selectfile-tree {
  height: 95%;
  width: 310px;
  overflow: auto;
}
.tree-select {
  padding-top: 5px;
}
.tree-select .tree-select-title {
  display: inline-block;
  font-size: 16px;
  color: #E5E5E5;
  position: relative;
  margin: 10px;
  margin-left: 15px;
}
.tree-select .tree-select-title:hover {
  cursor: pointer;
}
.tree-select .tree-select-title .triangle {
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-top: 8px solid #E5E5E5;
  border-radius: 2px;
  position: absolute;
  margin-right: 10px;
  top: 5px;
}
.tree-select .tree-select-title .triangle-reverse {
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-left: 8px solid #E5E5E5;
  border-radius: 2px;
  position: absolute;
  margin-right: 10px;
  left: 5px;
  top: 3px;
}
.tree-select .tree-select-title .title-text {
  margin-left: 20px;
}
.tree-select .tree-select-box .select-value {
  padding-left: 20px;
  font-size: 14px;
  color: #FFFFFF;
  line-height: 26px;
  height: 26px;
}
.tree-select .tree-select-box .select-value:hover {
  background: #1267CB;
  cursor: pointer;
}
.header-box {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.header-box .title-box {
  -webkit-flex: auto;
      -ms-flex: auto;
          flex: auto;
  font-size: 14px;
  color: #FFFFFF;
}
.header-box .button-box {
  margin: auto 0;
  height: 20px;
  float: right;
}
.header-box .button-box img {
  margin-left: 15px;
  height: 20px;
  width: 20px;
  cursor: pointer;
}
.header-box .button-box .btn-icon {
  display: inline-block;
  position: relative;
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.header-box .button-box .btn-icon img {
  margin-left: 0;
}
.header-box .button-box .btn-icon input {
  position: absolute;
  left: 0px;
  top: 0px;
  opacity: 0;
  z-index: 2;
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.children-box {
  margin: auto 20px;
}
.children-box .input-title {
  text-align: left;
  font-size: 14px;
  color: #FFFFFF;
  margin-top: 30px;
  margin-bottom: 20px;
}



